* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

ul {
  list-style: none;
}

.country-info {
  margin-left: 15px;
display: flex;
flex-direction: column;
}

.country-name {
  font-size: 24px;
  font-weight: 500;
}

.country-des {
  font-size: 18px;
  font-weight: 500;
  text-transform: uppercase;
}

.country-list {
  display: flex;
  flex-direction: column;
 justify-content: center;
 gap: 30px;
}

.list-item {
  display: flex;
  gap: 15px;
}